.guideline {
  height: 0.3125rem;
  width: 5.208333rem;
  margin: 0.432292rem auto;
  background: #fff;
  line-height: 0.3125rem;
  -webkit-box-shadow: 0 0.026042rem 0.052083rem rgba(0,0,0,0.25);
          box-shadow: 0 0.026042rem 0.052083rem rgba(0,0,0,0.25);
  border-radius: 0.260417rem;
}
.guideline nav {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.guideline nav label {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100%;
  z-index: 1;
  position: relative;
  cursor: pointer;
}
.guideline nav label a {
  position: relative;
  z-index: -1;
  color: #1d1f20;
  font-size: 0.130208rem;
  font-weight: 500;
  text-decoration: none;
  -webkit-transition: color 0.6s ease;
  transition: color 0.6s ease;
}
.guideline nav #home:checked ~ label.home a {
  color: #fff;
}
.guideline nav label a img {
  height: 0.145833rem;
  margin: -0.015625rem 0.130208rem;
}
.guideline nav .tab {
  position: absolute;
  height: 100%;
  width: 20%;
  left: 0;
  bottom: 0;
  /* background: linear-gradient(45deg, #05abe0 0%, #8200f4 100%); */
  /* background: linear-gradient(-225deg, #FFE29F 0%, #FFA99F 48%, #FF719A 100%); */
  /* background-image: linear-gradient(to right, #ffc3a0 0%, #ffafbd 100%); */
  /* background-image: linear-gradient(to right, #ed6ea0 0%, #ec8c69 100%); */
  background-image: -webkit-gradient(linear, left bottom, left top, from(#209cff), to(#68e0cf));
  background-image: linear-gradient(to top, #209cff 0%, #68e0cf 100%);
  border-radius: 0.260417rem;
  -webkit-transition: 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.guideline nav #home:checked ~ label.home a,
.guideline nav #search:checked ~ label.search a,
.guideline nav #browse:checked ~ label.browse a,
.guideline nav #rank:checked ~ label.rank a,
.guideline nav #memory:checked ~ label.memory a {
  color: #fff;
}
.guideline nav #search:checked ~ .tab {
  left: 20%;
}
.guideline nav #browse:checked ~ .tab {
  left: 40%;
}
.guideline nav #rank:checked ~ .tab {
  left: 60%;
}
.guideline nav #memory:checked ~ .tab {
  left: 80%;
}
.guideline nav input {
  display: none;
}
